import { createContext, FC, useContext, useMemo, useState } from "react";

import { LeadDataFieldInterface } from "Shared/Interfaces/LeadDataFieldsInterface";

import {
  LeadDataFieldApiContextState,
  LeadDataFieldDataContextState,
} from "./types";

const LeadDataFieldData = createContext<
  LeadDataFieldDataContextState | undefined
>(undefined);

const LeadDataFieldApi = createContext<
  LeadDataFieldApiContextState | undefined
>(undefined);

const LeadDataFieldsProvider: FC<any> = ({ children }) => {
  const [leadDataFields, setLeadDataFields] = useState<
    LeadDataFieldInterface[]
  >([]);
  const [leadDataFilter, setLeadDataFilter] = useState<string>("");
  const [editLeadDataId, setEditLeadDataId] = useState<number | undefined>();
  const [showLeadDataForm, setShowLeadDataForm] = useState<boolean>(false);
  const data: LeadDataFieldDataContextState = {
    leadDataFields,
    leadDataFilter,
    showLeadDataForm,
    editLeadDataId,
  };
  const api = useMemo<LeadDataFieldApiContextState>(
    () => ({
      setLeadDataFields,
      setLeadDataFilter,
      setShowLeadDataForm,
      setEditLeadDataId,
    }),
    []
  );

  return (
    <LeadDataFieldData.Provider value={data}>
      <LeadDataFieldApi.Provider value={api}>
        {children}
      </LeadDataFieldApi.Provider>
    </LeadDataFieldData.Provider>
  );
};

export const useLeadDataFieldData = () => {
  const context = useContext(LeadDataFieldData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of LeadDataFieldsProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useLeadDataFieldApi = () => {
  const context = useContext(LeadDataFieldApi);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of LeadDataFieldsProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default LeadDataFieldsProvider;
