import { AuthenticatedReq } from "Utils/Axios";

export default class LeadDataFieldsApi {
  public static getLeadDataFields = async () => {
    return await AuthenticatedReq(`/lead-data-fields`, "GET");
  };

  public static getLeadDataField = async (leadDataFieldId: number) => {
    return await AuthenticatedReq(
      `/lead-data-fields/edit/${leadDataFieldId}`,
      "GET"
    );
  };

  public static createLeadDataField = async (data: any) => {
    return await AuthenticatedReq(`/lead-data-fields/`, "POST", { data });
  };

  public static updateLeadDataField = async (
    editLeadDataId: number,
    data: any
  ) => {
    return await AuthenticatedReq(
      `/lead-data-fields/update/${editLeadDataId}`,
      "PUT",
      { data }
    );
  };

  public static deleteLeadDataField = async (deleteLeadDataId: number) => {
    return await AuthenticatedReq(
      `/lead-data-fields/delete/${deleteLeadDataId}`,
      "DELETE"
    );
  };
}
